* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: var(--primary-color);
  display: flex;
  justify-content: center;
  font-family: "Montserrat", sans-serif;
  flex-direction: column;
  align-items: center;
}
:root {
  --primary-color: #fafafa;
  --secondary-color: #1e1823;
  --accent-color: #fd9222;
}
a {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
}
ul {
  list-style-type: none;
}

/* Header styles*/

.header {
  justify-content: center;
  padding: 20px;
  display: flex;
  gap: 101px;
}

.logo-wrapper {
  display: flex;
  align-items: center;
  gap: 5px;
}
.logo {
  width: 10.98px;
  height: 14.8px;
}
.title {
  font-style: italic;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
}
.sidebar {
  width: 34vh;
  height: 100vh;
  background-color: var(--secondary-color);
  color: var(--primary-color);
  position: fixed;
  top: 0px;
  right: -520px;
  transition: right 0.5s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 15;
}
.sidebar-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.sidebar-list {
  display: flex;
  flex-direction: column;
  gap: 24px;
  white-space: nowrap;
}
.sidebar a {
  transition: color 0.3s ease;
}

.sidebar a:hover {
  color: var(--accent-color);
}
.close-icon {
  display: none;
  border: none;
  width: 28px;
  height: 28px;
  background-color: transparent;
  cursor: pointer;
  position: fixed;
  top: 15px;
  transition: right 0.3s ease;
  transition: transform 0.3s ease;
  right: 15px;
  border-radius: 5px;
}
.close-icon:hover {
  transform: scale(1.1) rotate(90deg);
}
.social-icons {
  display: none;
  gap: 16px;
  bottom: 20px;
  right: 108px;
  position: fixed;
}
.social-icons-desktop {
  display: none;
}
.social-icons img {
  width: 20px;
  transition: background-color 0.3s ease, color 0.3s ease;
  height: 20px;
  border-radius: 5px;
  padding: 1px;
}
.social-icons img:hover {
  background-color: var(--accent-color);
}
.toggle-button {
  border: none;
  cursor: pointer;
  width: 21px;
  height: 18px;
  background-color: transparent;
  transition: transform 0.3s ease;
}
.toggle-button:hover {
  transform: scale(1.1);
}
.hero {
  display: flex;
  position: relative;
  justify-content: center;
}
.header-img {
  width: 335px;
}

.header-img img {
  object-fit: cover;
  width: 100%;
  height: auto;
  display: block;
  border-radius: 30px;
}
.hero-content {
  z-index: 1;
  flex-direction: column;
  align-items: center;
  gap: 32px;
  width: 100%;
  max-width: 300px;
  display: flex;
  position: absolute;
  bottom: 224px;
}
.hero-pharagraph {
  max-width: 300px;
  color: var(--primary-color);
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
  text-align: center;
  padding-left: 18px;
  text-align: left;
  padding: 25px;
}
.hero-btn {
  display: flex;
  flex-direction: row;
  gap: 14px;
  justify-content: center;
}

.buy-now-btn {
  width: 110px;
  height: 41px;
  border-radius: 50px;
  color: var(--primary-color);
  background-color: var(--accent-color);
  border: none;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  transition: transform 0.3s ease;
}
.buy-now-btn:hover,
.how-its-made-btn:hover {
  transform: scale(1.1);
  color: var(--accent-color);
  background-color: #fcf1e0;
}

.how-its-made-btn {
  width: 149px;
  height: 41px;
  border-radius: 50px;
  color: var(--primary-color);
  background-color: transparent;
  font-weight: 600;
  font-size: 14px;
  border: 1px solid var(--primary-color);
  cursor: pointer;
  transition: transform 0.3s ease;
}
.scroll-down-container {
  display: flex;
  gap: 12px;
  align-items: center;
  position: absolute;
  bottom: -118px;
  left: 30px;
  font-size: 12px;
  color: var(--primary-color);
}
.scroll-down-btn {
  width: 28px;
  height: 28px;
  border-radius: 14px;
  border: none;
  cursor: pointer;
}
.scroll-down-btn:hover {
  transform: scale(1.1);
}
.buy-now-modal {
  display: none;
  position: fixed;
  z-index: 5;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(54, 54, 54, 0.8);
  backdrop-filter: blur(5px);
  overflow: hidden;
}
.buy-now-modal-content {
  background-color: var(--primary-color);
  text-align: center;
  border: 1px solid #cec8c8;
  border-radius: 15px;
  padding: 15px;
  max-width: 320px;
  height: auto;
  margin: 2px auto;
  position: relative;
  overflow-y: auto;
}

.buy-now-modal-content span {
  font-size: 20px;
  font-weight: 600;
}
.buy {
  color: var(--accent-color);
}
.close-modal {
  position: absolute;
  border: none;
  background-color: transparent;
  top: 10px;
  right: 15px;
  font-size: 24px;
  cursor: pointer;
}

.products-list {
  display: flex;
  flex-direction: column;
  gap: 5px;
  border: none;
  background-color: transparent;
  margin-top: 15px;
  justify-content: center;
  align-items: center;
}
.modal-img {
  cursor: pointer;
  background-color: #fff;
  border-radius: 20px;
  display: flex;
  align-items: center;
  gap: 110px;
  border: 1px solid #cec8c8;
  transition: transform 0.3s ease;
}

.modal-img:hover {
  transform: scale(1.1);
  border: 1px solid var(--accent-color);
}
.modal-img span {
  background-color: var(--primary-color);
  font-size: 13px;
  width: 70px;
  height: 29px;
  padding: 5px;
  border-radius: 100px;
  margin-right: 10px;
  border: 1px solid #ededed;
}
.input-group {
  text-align: left;
}

.modal-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 15px;
  width: 100%;
  gap: 10px;
}

.modal-form label {
  font-size: 12px;
  font-weight: 500;
  color: #333;
}

.modal-form input,
.modal-form textarea {
  width: 263px;
  height: 40px;
  border-radius: 8px;
  border: 1px solid #cec8c8;
  padding-left: 18px;
  padding: 10px;
  font-size: 13px;
  font-family: "Montserrat", sans-serif;
  display: block;
}
.modal-form input::placeholder,
textarea::placeholder {
  color: #aaa;
}

.modal-form button {
  width: 100px;
  height: 35px;
  color: var(--primary-color);
  background-color: var(--accent-color);
  border-radius: 50px;
  border: none;
  cursor: pointer;
}
.input-wrapper {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  gap: 2px;
}
.iti__country-list {
  width: 261px;
}

/* Advantages styles*/
.advantages-wrapper {
  padding: 20px;
  background-color: var(--secondary-color);
  margin-top: 70px;
  width: 100%;
  display: flex;
  justify-content: center;
}
.logo-container {
  width: 62px;
  height: 62px;
  border-radius: 50%;
  border: 1px solid #aaa;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}
.logo-container img {
  width: 32px;
  height: 32px;
}
.advantages-title {
  color: var(--primary-color);
  font-weight: 600;
  font-size: 18px;
  margin-top: 20px;
  margin-bottom: 18px;
}
.advantages-pharagraph {
  font-weight: 400;
  font-size: 14px;
  color: var(--primary-color);
}
.advantages-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 375px;
}
/* Tablet */

@media screen and (min-width: 768px) {
  .header {
    justify-content: space-evenly;
    gap: 216px;
  }
  .header-img {
    width: 704px;
  }

  .scroll-down-container {
    bottom: 6px;
    width: 100%;
    font-size: 14px;
    left: 365px;
  }

  .hero-content {
    align-items: flex-end;
    bottom: 40px;
  }
  .hero-pharagraph {
    font-size: 28px;
    line-height: 34px;
    width: 100%;
    margin-left: 280px;
    max-width: none;
  }
  .hero-btn {
    margin-right: 205px;
  }
  .hero-content-wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .buy-now-modal-content {
    max-width: 600px;
  }

  .products-list {
    flex-direction: row;
    gap: 15px;
  }
  .modal-img {
    gap: 8px;
    width: 156px;
    height: 129px;
    flex-direction: column;
  }
  .modal-img img {
    width: 100px;
  }
  .personal-info {
    flex-direction: row;
    align-items: flex-end;
    gap: 10px;
  }

  .personal-info input {
    width: 246px;
    height: 40px;
  }
  .input-group {
    display: flex;
    flex-direction: column;
    gap: 6px;
  }
  .input-group label {
    padding-right: 87px;
  }

  .email input,
  .phone input,
  .card input,
  .comment textarea {
    width: 500px;
  }
  /* Advantages styles*/
  .advantages-wrapper {
    height: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .advantages-list {
    max-width: 305px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
    justify-content: center;
    align-items: center;
    max-width: 715px;
  }
}
/* Desktop */
@media screen and (min-width: 1280px) {
  .header {
    gap: 0px;
  }
  .header-img {
    width: 1136px;
  }
  .logo-wrapper {
    width: 75%;
  }
  .toggle-button {
    display: none;
  }
  .sidebar {
    position: static;
    background-color: transparent;
    height: auto;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 0;
  }

  .sidebar-content {
    flex-direction: row;
    gap: 40px;
  }

  .sidebar-list {
    display: flex;
    flex-direction: row;
    color: var(--secondary-color);
  }
  .sidebar-list a {
    transition: transform 0.3s ease;
  }

  .sidebar-list a:hover {
    transform: scale(1.2);
  }

  .hero-content-wrapper {
    display: flex;
    justify-content: center;
    min-width: 400px;
  }
  .scroll-down-container {
    left: 543px;
  }

  .hero-btn {
    margin-right: 410px;
  }

  .hero-pharagraph {
    width: 100%;
    max-width: 88%;
    margin-right: 150px;
  }
  .social-icons {
    display: none;
  }
  .social-icons-desktop {
    display: flex;
    gap: 16px;
    align-items: center;
  }
  .social-icons-desktop img {
    width: 20px;
    height: 18px;
    transition: transform 0.3s ease;
  }
  .social-icons-desktop img:hover {
    transform: scale(1.2);
  }

  /* Advantages styles*/

  .advantages-list {
    grid-template-columns: repeat(3, 1fr);
    align-items: baseline;
    max-width: 1156px;

    grid-auto-flow: row;
  }
  .border {
    border-right: 1px solid #aaa;
    padding: 10px;
  }
}
